import { InjectionKey } from 'vue'
import { createStore, Store } from 'vuex'

declare module '@vue/runtime-core' {
  // declare your own store states
  export interface State {
    network: 'ethereum' | 'vechain'
  }

  // provide typings for `this.$store`
  interface ComponentCustomProperties {
    $store: Store<State>
  }
}

export interface State {
  network: 'ethereum' | 'vechain',
  addresses: Record<string, string | null>
}

// define injection key
export const key: InjectionKey<Store<State>> = Symbol('v')

export const store = createStore<State>({
  state () {
    return {
      network: 'ethereum',
      addresses: {
        vet: null,
        eth: null
      }
    }
  },
  getters: {
    currentAddress (state: State) {
      return state.network === 'ethereum' ? state.addresses.eth : state.addresses.vet
    }
  },
  mutations: {
    changeNetwork (state) {
      if (state.network === 'ethereum') {
        state.network = 'vechain'
      } else {
        state.network = 'ethereum'
      }
    },
    setAddress (state, payload: { key: 'vet' | 'eth', addr: string }) {
      state.addresses[payload.key] = payload.addr
    }
  }
})
