
import { h } from 'vue'
import { Options, Vue } from 'vue-class-component'
import * as config from '../config'
import Transfer from './history/Transfer.vue'
import Receive from './history/Receive.vue'
import { useDialog, DialogApi } from 'naive-ui'

@Options({
  components: {
    Transfer,
    Receive
  },
  data () {
    return {
      tab: 'ongoing',
      ongoingList: [],
      completedList: [],
      dialog: null
      // showConfirm: false
    }
  },
  computed: {
    network (): 'vechain' | 'ethereum' {
      return this.$store.state.network
    },
    address () {
      return this.$store.getters.currentAddress
    }
  },
  watch: {
    network () {
      this.fetchList(20, 0)
    },
    address () {
      this.fetchList(20, 0)
    },
    tab () {
      this.fetchList(20, 0)
    }
  }
})
export default class History extends Vue {
  network!: 'vechain' | 'ethereum'
  address!: string
  tab!: 'ongoing' | 'completed'
  ongoingList!: unknown[]
  dialog!: DialogApi
  completedList!: unknown[]

  async fetchList (limit: number, offset: number): Promise<void> {
    const params: Record<string, string | number> = {
      chainname: this.network,
      chainid: config[this.network].chainId,
      address: this.address,
      filter: this.tab,
      limit,
      offset
    }

    const queryStr = Object.keys(params).map((key: string) => key + '=' + params[key]).join('&')
    const response = await fetch(`${config.api.baseUrl}/history?${queryStr}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json'
      }
    })
    const resp = await response.json()
    if (this.tab === 'completed') {
      this.completedList = resp.history
    } else {
      this.ongoingList = resp.history
    }
  }

  created (): void {
    this.dialog = useDialog()
  }

  mounted (): void {
    this.fetchList(20, 0)
  }

  async claim (id: string, swapTx: string): Promise<void> {
    try {
      const temp = await fetch(`${config.api.baseUrl}/merkleproof?bridgetxid=${id}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      const resp = await temp.json()
      const meta = resp.meta
      let response
      if (this.network === 'vechain') {
        response = await (await this.$vet.claim(meta.receipt, meta.amount, meta.merkleProof, meta.swapCount, meta.root, meta.token, this.address)).txid
      } else {
        response = await (await this.$eth.claim(meta.receipt, meta.amount, meta.merkleProof, meta.swapCount, meta.root, meta.token, this.address)).hash
      }
      console.log(response)
      window.localStorage.setItem(swapTx, response)
    } catch (error) {
      console.log(error)
    }
  }

  onClaim ([id, swapTx]: string[]): void {
    const hasClaimed = !!window.localStorage.getItem(swapTx)
    if (hasClaimed) {
      this.dialog.warning({
        title: 'Claim again?',
        content: () => {
          return h('div', null,
            [
              'Your transaction may still be processing, it may take a while if the network has high volume of transactions.',
              h('br'),
              'You can check on your transaction',
              h(
                'a',
                {
                  target: '_blank',
                  href: 'https://explore-testnet.vechain.org/' // TODO
                },
                [' in the explorer']
              )
            ])
        },
        positiveText: 'Claim',
        negativeText: 'I will wait',
        onPositiveClick: () => {
          this.claim(id, swapTx)
        }
      })
    } else {
      this.claim(id, swapTx)
    }
  }
}
