
import { Options, Vue } from 'vue-class-component'
import Eth from './icons/Eth.vue'
import Vet from './icons/Vet.vue'
import Address from './Address.vue'
import Connect from './Connect.vue'
import Disconnect from './Disconnect.vue'
import SwitchNetwork from './SwitchNetwork.vue'

@Options({
  props: {
    addresses: Object,
    network: String
  },
  components: {
    Eth,
    Vet,
    Address,
    Connect,
    SwitchNetwork,
    Disconnect
  },
  computed: {
    logo (): string {
      if (this.network === 'ethereum') {
        return 'Eth'
      } else {
        return 'Vet'
      }
    },
    text (): string {
      return this.network === 'vechain' ? this.addresses.vet : this.addresses.eth
    }
  }
})
export default class ConnectStatus extends Vue {
  addresses!: Record<'vet' | 'eth', string | null>
  network!: 'vechain' | 'ethereum'
  logo!: string
}
