
import { Options, Vue } from 'vue-class-component'
import Address from './Address.vue'
import ethereum from './icons/Ethereum.vue'
import vechain from './icons/Vechain.vue'
import { BigNumber, utils } from 'ethers'
import { Loader, AlertCircle, CircleCheck } from '@vicons/tabler'

type RPCError = {
  code: number
  message: string
}

@Options({
  data () {
    return {
      show: null,
      needApprove: null,
      status: '',
      active: 0,
      checking: null,
      approving: false,
      approved: false,
      myPromise: null,
      rejectMsg: {
        title: 'User Canceled',
        // eslint-disable-next-line quotes
        msg: `You'd reject to sign the transaction`
      }
    }
  },
  components: {
    Address,
    ethereum,
    vechain,
    CircleCheck,
    AlertCircle,
    Loader
  },
  props: {
    content: Array
  },
  computed: {
    network (): 'ethereum' | 'vechain' {
      return this.$store.state.network
    }
  }
})
export default class ConfirmDialog extends Vue {
  show!: boolean
  needApprove!: boolean
  approving!: boolean
  checking!: boolean
  status!: 'confirming' | 'rejected' | 'signed' | ''
  approved!: boolean
  active!: number
  content!: {
    wallet: string,
    token: string | null,
    amount: string,
    symbol: string,
    network: 'vechain' | 'ethereum'
  }[]
  rejectMsg!: {
    title: string,
    msg: string
  }
  myPromise!: Promise<unknown>
  resolve!: (v: any) => void
  reject!: (v: any) => void

  async statusCheck (): Promise<void> {
      this.checking = true
      const agent = this.$eth
      const isAllowanceOk = await this.checkAllowance()
      if (!isAllowanceOk || this.checkLocalTx()) {
        this.needApprove = true
        if (isAllowanceOk === false && this.checkLocalTx()) {
          const [from] = this.content!
          const lastApprStr = localStorage.getItem(`lastApprove-${from.symbol}`)
          const lastAppr = lastApprStr ? JSON.parse(lastApprStr) : ''
          this.checking = false
          try {
            this.approving = true
            this.approved = true
            const recipet = await agent.provider.waitForTransaction(lastAppr.txid, 1)
            this.approving = false
            this.approved = recipet.status === 1
          } catch (error) {
            if (this.show) {
              this.status = 'rejected'
            }
          }
        }
      } else {
        this.needApprove = false
      }
      this.checking = false
    }
  onShow () {
    this.statusCheck()
  }

  // show
  popUp (): Promise<any> {
    this.show = true
    const result = new Promise((resolve, reject) => {
        this.resolve = resolve
        this.reject = reject
      })
    this.myPromise = result
    return result
  }

  onClose (): void {
    this.reject && this.reject('closed')
    this.needApprove = false
    this.approved = false
    this.approving = false
    this.active = 0
    this.checking = true
    this.status = ''
    this.rejectMsg = {
      title: 'User Canceled',
      // eslint-disable-next-line quotes
      msg: `You'd reject to sign the transaction`
    }
    this.resolve && this.resolve('')
    this.reject && this.reject('')
    this.myPromise = null as unknown as Promise<unknown>
    this.resolve = null as unknown as () => void
    this.reject = null as unknown as () => void
  }

  checkLocalTx (): boolean {
    const [from] = this.content
    const lastApprStr = localStorage.getItem(`lastApprove-${from.symbol}`)
    const lastAppr = lastApprStr ? JSON.parse(lastApprStr) : ''
    return lastAppr && lastAppr.signer === from.wallet && BigNumber.from(from.amount).lte(lastAppr.amount)
  }

  async checkAllowance (): Promise<boolean> {
    let result = false
    try {
      const [from] = this.content
      console.log(from)
      if (from.network === 'ethereum' && from.token) {
        const token = this.$eth.getToken(from.token)
        this.checking = true
        const allowance = await token.allowance(from.wallet)
        result = allowance.gte(utils.parseEther(from.amount))
      } else {
        result = true
      }
    } catch (error) {
      console.error('On check allowance', error)
    }
    return result
  }

  async onApporve (): Promise<void> {
    const from = this.content[0]
    const token = this.$eth.getToken(from.token!)
    try {
      this.status = 'confirming'
      const tx: any = await token.approve(from.amount, from.wallet)
      localStorage.setItem(`lastApprove-${from.symbol}`, JSON.stringify({
        txid: tx.hash,
        amount: from.amount,
        signer: from.wallet
      }))
      this.approving = true

      const temp = new Promise((resolve) => {
        this.$eth.provider.waitForTransaction(tx.hash, 1).then(recipet => {
          this.approving = false
          this.approved = recipet.status === 1
        }).finally(() => {
          localStorage.removeItem(`lastApprove-${from.symbol}`)
          resolve('finished')
        })
      })
    } catch (error) {
      if (this.show) {
        console.log('on approve', error)
        this.status = 'rejected'
      }
    }
  }

  async signASend (): Promise<any> {
    const [from, to] = this.content
    if (from.network === 'ethereum') {
      return this.$eth.swap(to.wallet, from.amount, from.wallet, from.token)
    } else {
      return this.$vet.swap(to.wallet, from.amount, from.wallet, from.token, from.symbol)
    }
  }

  async swap (): Promise<void> {
    const from = this.content[0]
    try {
      this.status = 'confirming'
      const tx = await Promise.race([this.signASend(), this.myPromise])
      this.resolve && this.resolve('')

      console.log('TODO tx link:', tx)
      this.status = 'signed'
    } catch (error) {
      console.log('error on swap:', error)
      const err = error as RPCError
      if (from.network === 'vechain') {
        // TODO
      } else {
        if (err.code === 4004) {
          console.error('user denied')
        } else {
          this.rejectMsg = {
              title: 'Transaction Rejected',
              msg: err.message
            }
        }
        if (this.show) {
          this.status = 'rejected'
        }
      }
    }
  }
}
