
import { Options, Vue } from 'vue-class-component'
import Metamask from './icons/Metamask.vue'
import Sync from './icons/Sync.vue'

@Options({
  props: {
    network: String,
    text: String,
    dialog: Boolean
  },
  components: {
    Metamask,
    Sync
  }
})
export default class Disconnect extends Vue {
  network!: 'ethereum' | 'vechain'
  text!: string

  onDisconnect (): void {
    try {
      this.$store.commit('setAddress', { key: this.network === 'vechain' ? 'vet' : 'eth', addr: null })
      if (!this.$store.getters.currentAddress) {
        this.$store.commit('changeNetwork')
      }
    } catch (error) {
      console.log(error)
    }
  }
}
