
import { Copy } from '@vicons/tabler'
import { Options, Vue } from 'vue-class-component'

@Options({
  props: {
    content: String
  },
  components: {
    Copy
  }
})
export default class SwitchNetwork extends Vue {
  content!: string

  onCopy (): void {
    window.navigator.clipboard.writeText(this.content).then(() => console.log('copied'))
  }
}
