
import { Options, Vue } from 'vue-class-component'
import Metamask from './icons/Metamask.vue'
import Sync from './icons/Sync.vue'

@Options({
  props: {
    network: String,
    text: String,
    dialog: Boolean
  },
  components: {
    Metamask,
    Sync
  }
})
export default class Connect extends Vue {
  showM = false
  network!: 'ethereum' | 'vechain'
  text!: string
  dialog!: boolean

  onClick (): void {
    if (this.dialog) {
      this.showM = true
    } else {
      this.onConnect()
    }
  }

  async onConnect (): Promise<void> {
    try {
      let address
      if (this.network === 'vechain') {
        address = await this.$vet.connect()
        console.log(address)
      } else if (this.network === 'ethereum') {
        address = await this.$eth.connect()
      }
      this.showM = false
      this.$store.commit('setAddress', { key: this.network === 'vechain' ? 'vet' : 'eth', addr: address })
    } catch (error) {
      console.log(error)
    }
  }
}
