import { BigNumber, Contract, providers, utils } from 'ethers'
import { balanceOf, Swap, SwapNativeCoin, approve, allowance, Claim, ClaimNativeCoin } from './Abi'
import { ethereum } from '../config'

export default class BridgeOnEth {
  private targetId!: string
  private eth!: typeof window.ethereum
  provider!: providers.Web3Provider
  private _bridge!: Contract
  private _token!: Contract
  private _signer = '0x0000000000000000000000000000000000000000'

  constructor (targetId: string) {
    if (window.ethereum && window.ethereum.isMetaMask) {
      this.targetId = targetId
      this.eth = window.ethereum
      this.provider = new providers.Web3Provider(window.ethereum)
      this._bridge = new Contract(ethereum.contracts.ftBridge, [Swap, SwapNativeCoin, Claim, ClaimNativeCoin])
    } else {
      // TODO
    }
  }

  async getAccount () : Promise<string> {
    if (!window.ethereum || !window.ethereum.isMetaMask) {
      throw new Error('MetaMask required')
    }
    const account = await this.eth.request({
      method: 'eth_accounts'
    })
    return account.length ? account[0] : ''
  }

  async requestAccount (): Promise<string> {
    if (!window.ethereum.isMetaMask) {
      throw new Error('MetaMask required')
    }
    const result = await this.eth.request({
      method: 'eth_requestAccounts'
    })
    return result[0] || ''
  }

  async connect (): Promise<string> {
    let result = ''
    result = await this.getAccount()
    result = result || await this.requestAccount()
    return result
  }

  getToken (addr: string) {
    const token = new Contract(addr, [balanceOf, approve, allowance])
    return {
      allowance: async (signer: string): Promise<BigNumber> => {
        const _signer = this.provider.getSigner(signer)
        return await token.connect(_signer).allowance(signer, ethereum.contracts.ftBridge)
      },
      approve: async (amount: string, signer: string): Promise<unknown> => {
        const _signer = this.provider.getSigner(signer)
        return await token.connect(_signer).approve(ethereum.contracts.ftBridge, utils.parseEther(amount))
      },
      balance: async (addr: string): Promise<BigNumber> => {
        const _signer = this.provider.getSigner(addr)
        return await token.connect(_signer).balanceOf(addr)
      }
    }
  }

  async claim (address: string, amount: string, mp: string[], swapCount: string, root: string, token: string, signer: string) {
    const _signer = this.provider.getSigner(signer)
    if (token) {
      return await this._bridge.connect(_signer).claim(token, address, amount, swapCount, root, mp)
    } else {
      return await this._bridge.connect(_signer).claimNativeCoin(address, amount, swapCount, root, mp)
    }
  }

  async swap (to: string, amount: string, signer: string, token?: string | null) {
    const _signer = this.provider.getSigner(signer)

    // token 18 decimals
    if (token) {
      return await this._bridge.connect(_signer).swap(token, utils.parseEther(amount), to)
    } else {
      return await this._bridge.connect(_signer).swapNativeCoin(to, { value: utils.parseEther(amount) })
    }
  }
}
